.forte{

    background-image: url(../../img/raw_jpeg/forte.png);
    aspect-ratio: 39 / 24;
    z-index: 1;
    background-repeat: no-repeat;
    background-size: contain;
    background-clip: padding-box;
    border-radius: .2rem;
    min-width: 500px;
    min-height: auto;
    transition: all .3s;
    display: inline-block;
    position: absolute;
    bottom: 0;
    overflow: hidden;
    scale: 1;

  }
  @media screen and (min-width: 0px) and (max-width: 768px) {.forte{
display: none;
  }}